@import '../../styles/colors.scss' ;
.blackFilter{
    width: 100%;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    background-color: $blackFilter;
    z-index: 10;
}
.img{
    max-width: 90vw;
    max-height: 90vh;
    z-index: 11;
    position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}